import React from "react";

interface TestimonyComponentProps {
  personName: string;
  children: React.ReactNode;
}

const TestimonyComponent: React.FC<TestimonyComponentProps> = ({
  personName,
  children,
}) => {
  return (
    <div className="w-full md:w-1/3 p-4 flex">
      <div className="bg-white shadow-lg p-6 rounded-lg flex flex-col flex-grow">
        <p>{children}</p>
        <p className="mt-4 font-semibold">- {personName}</p>
      </div>
    </div>
  );
};

export default TestimonyComponent;
