import React from "react";
import { NavLink } from "react-router-dom";
import ProductComponent from "components/ProductComponent";
import TestimonyComponent from "components/TestimonyComponent";

const LandingPage: React.FC = () => {
  const signupUrl = process.env.REACT_APP_SIGNUP_URL || "#";

  return (
    <div>
      {/* Hero Section */}
      <header className="bg-blue-600 text-white text-center px-4 py-20">
        <div className="container mx-auto">
          <h1 className="text-4xl font-bold mb-4">
            Sederhanakan Pembayaran Kasir Anda dengan QRIS
          </h1>
          <p className="text-xl mb-8">
            Mempermudah pembuatan pesanan, inventaris, dan pelacakan keuangan
            bisnis Anda
          </p>
          <NavLink
            to="/service"
            className="bg-white text-blue-600 font-semibold py-2 px-4 rounded"
          >
            Pelajari Lebih Lanjut
          </NavLink>
        </div>
      </header>

      {/* Features Section */}
      <main>
        <section id="features" className="py-20 bg-gray-100 px-4">
          <div className="container mx-auto text-center">
            <h2 className="text-3xl font-bold mb-8">Penawaran Terbaik Kami</h2>
            <div className="flex flex-wrap justify-center items-stretch">
              <ProductComponent title="Manajemen Pesanan">
                Manajemen Pesanan memudahkan pembuatan dan modifikasi pesanan,
                pelacakan status pesanan, serta pembuatan dan pengiriman struk
                dan faktur melalui email, memastikan proses yang efisien dan
                teratur.
              </ProductComponent>
              <ProductComponent title="Pemrosesan Pembayaran">
                Pemrosesan Pembayaran dengan Viartha memungkinkan Anda memproses
                pembayaran menggunakan QRIS dan berbagai metode lain seperti
                kartu kredit/debit, e-wallet, dan transfer bank. Semua transaksi
                dicatat dalam riwayat pembayaran untuk kemudahan pengelolaan
                keuangan.
              </ProductComponent>
              <ProductComponent title="Pelaporan Keuangan">
                Pelaporan Keuangan Viartha menyediakan laporan penjualan
                terperinci untuk berbagai periode, pelacakan pengeluaran bisnis,
                serta laporan Laba Rugi untuk analisis kinerja bisnis yang lebih
                baik.
              </ProductComponent>
            </div>
          </div>
        </section>

        {/* Testimonials Section */}
        <section id="testimonials" className="py-20 px-4">
          <div className="container mx-auto text-center">
            <h2 className="text-3xl font-bold mb-8">Apa Kata Pengguna Kami</h2>
            <div className="flex flex-wrap justify-center items-stretch">
              <TestimonyComponent personName="Pemilik Bisnis A">
                Platform ini telah menyederhanakan pemrosesan pembayaran kami
                seperti tidak pernah sebelumnya!
              </TestimonyComponent>
              <TestimonyComponent personName="Pemilik Bisnis B">
                Alat yang wajib dimiliki untuk bisnis apapun di Indonesia.
                Sangat direkomendasikan!
              </TestimonyComponent>
            </div>
          </div>
        </section>
      </main>

      {/* Call to Action Section */}
      <footer className="bg-blue-600 text-white text-center px-4 py-20">
        <div className="container mx-auto">
          <h2 className="text-3xl font-bold mb-4">Mulai Hari Ini</h2>
          <p className="text-xl mb-8">
            Bergabunglah dengan banyak bisnis yang sudah mendapatkan manfaat
            dari platform kami.
          </p>
          <a
            href={signupUrl}
            className="bg-white text-blue-600 font-semibold py-2 px-4 rounded"
          >
            Daftar Sekarang
          </a>
        </div>
      </footer>
    </div>
  );
};

export default LandingPage;
